import { Mark, Data } from '@ali/4ever-cangjie';
import { MARK_TYPE_DATA, MARK_TYPE_VIEW } from '../types';

interface CommentMarkData extends Data {
  contentId: string;
}

interface ViewMarkData extends CommentMarkData {
  count: number;
  summary: string;
  position?: string;
  isActive?: boolean;
  isNew?: boolean;
  isReadonly?: boolean;
}

export type ViewMark = Mark<ViewMarkData>;

export type DataMark = Mark<CommentMarkData>;

export const DataMark = {
  TYPE: MARK_TYPE_DATA,

  create(data: CommentMarkData): DataMark {
    return Mark.create<CommentMarkData>({
      type: DataMark.TYPE,
      data,
    });
  },

  isDataMark(maybeMark: any): maybeMark is DataMark {
    return Mark.isMark(maybeMark) && maybeMark.type === DataMark.TYPE;
  },
};

export const ViewMark = {
  TYPE: MARK_TYPE_VIEW,

  create(data: ViewMarkData): ViewMark {
    return Mark.create<ViewMarkData>({
      type: ViewMark.TYPE,
      data,
    });
  },

  isViewMark(maybeMark: any): maybeMark is ViewMark {
    return Mark.isMark(maybeMark) && maybeMark.type === ViewMark.TYPE;
  },
};
